	top: -10px;
	left: 0px;
	width: 100%;
	height: 10px;
	background-color: #CBE4DE;
}
.section-comparison-table table td:first-child{
	background-color: #F2F4F3;
	border-left: 6px solid #CBE4DE;
}
.section-comparison-table table td:last-child{
	background-color: #CBE4DE;
	border-bottom: 3px solid #C7DAD6;
}
.section-comparison-table table tr:last-child td:last-child{
	position: relative;
	border-left: 0px;
	border-bottom: 3px solid #CBE4DE;
}
.section-comparison-table table tr:last-child td:last-child:after{
	content: '';
	position: absolute;
	bottom: -13px;
	left: 0px;
	width: 100%;
	height: 10px;
	background-color: #CBE4DE;
}
.section-comparison-table table td{
	background-color: #ffffff;
	border-top: 0px;
	border-bottom: 3px solid #CBE4DE;
	border-left: 3px solid #CBE4DE;
	vertical-align: middle;
}
.section-comparison-table table td:nth-child(2){
	border-left: 0px;
}
.section-comparison-table table td i{
	color: #BDBDBD;
	font-size: 29px;
}
