	padding: 1px;
	background-color: #F4F6F4;
}
/* p-video-controls */
.p-video-controls, .p-video-controls > * {
	padding: 0;
	margin: 0;
}
.p-video-controls {
	overflow:hidden;
	background:transparent;
	width:100%;
	height:8.0971659919028340080971659919028%; /* of figure's height */
	position:relative;
}
.p-video-controls[data-state=hidden] {
	display:none;
}
.p-video-controls[data-state=visible] {
	display: -webkit-box;
    display: -ms-flexbox;
	display: flex;
    align-items: center;
    padding: 0px 5px 5px;
    margin-top: -7px;
}
.p-video-controls > * {
	float:left;
	width:3.90625%;
	height:100%;
	margin-left:0.1953125%;
	display:block;
}
.p-video-controls > *:first-child {
	margin-left:0;
}
.p-video-controls #progress-vid{
	height: 13px;
	background-color: #ffffff;
}
.p-video-controls .progress-cl {
	cursor: pointer;
	width: 100%;
	margin-right: 11px;
	margin-left: 11px; 
	border-radius: 0px;
	background-color: transparent;
	border: 2px solid #CBE4DE;
}
.p-video-controls button {
	text-align:center;
	overflow:hidden;
	white-space:nowrap;
  	text-overflow:ellipsis;
  	border:none;
  	cursor:pointer;
  	text-indent:-99999px;
  	background:transparent;
  	background-size:contain;
  	background-repeat:no-repeat;
  	height: fit-content;
}
.p-video-controls button:hover, .p-video-controls button:focus {
	opacity:0.8;
}
.p-video-controls button{
	width: 30px;
}
.p-video-controls button[data-state="play"] {
	background-image: url('https://cdn.shopify.com/s/files/1/0441/1431/3365/files/play-2.png?v=1602063545');
}
.p-video-controls button[data-state="pause"] {
	background-image: url('https://cdn.shopify.com/s/files/1/0441/1431/3365/files/pause.png?v=1602063545');
}
.p-video-controls button[data-state="mute"] {
	background-image: url('https://cdn.shopify.com/s/files/1/0441/1431/3365/files/mute.png?v=1602063545');	
}
.p-video-controls button[data-state="unmute"] {
	background-image: url('https://cdn.shopify.com/s/files/1/0441/1431/3365/files/speaker2.png?v=1602063545');	
}
.p-video-controls progress {
	display: block;
	width: 100%;
	height: 70%;
	margin-top: 0em;
	overflow: hidden;
	-moz-border-radius: 0px;
	-webkit-border-radius: 0px;
	border-radius: 0px;
	color: #0095dd; /* Internet Explorer uses this value as the progress bar's value colour */
}
.p-video-controls progress[data-state="fake"] {
	background: #ffffff;
	height:65%;
}
.p-video-controls progress span {
	width: 0%;
	height: 100%;
	display: inline-block;
	background-color: #ffffff;	
}
.p-video-controls progress::-moz-progress-bar-vid {
	background-color: #CBE4DE;
}
/* Chrome requires its own rule for this, otherwise it ignores it */
.p-video-controls progress::-webkit-progress-value {
	background-color: #CBE4DE;
}



