
.button{
    display: inline-block;
    padding: 3px 20px 0;
    background: none;
    transition: 300ms;
    text-decoration: none;
    text-transform: uppercase;
    letter-spacing: 1px;
    min-width: 190px;
    text-align: center;
    border: none;
    position: relative;
    @media (max-width:991px) {
        min-width: 145px;
    }
    @media (max-width:767px) {
        padding: 0 25px;
    }
    &:hover{
        text-decoration: none;
    }
}


// Modifires
.button_primary{
    background-color: $primary-button-bg-color;
    color: $primary-button-text-color;
    line-height: $primary-button-height;
    border: 1px solid $primary-button-border-color;
    font-size: $primary-button-font-size;
    span{
        position: relative;
        z-index: 2;
    }
    &:before{
        content: '';
        position: absolute;
        height: 100%;
        width: 0;
        background-color: $primary-button-hover-bg-color;
        left: 0;
        top: 0;
        transition: 300ms;
        z-index: 1;
    }
    &:hover{
        color: $primary-button-hover-text-color;
        border: 1px solid $primary-button-hover-bg-color;
        &:before{
            width: 100%;
        }
    }
}
.button_atc{
    background-color: $atc-button-bg-color;
    color: $atc-button-text-color;
    line-height: $atc-button-height;
    border: 1px solid $atc-button-border-color;
    font-size: $atc-button-font-size;
    min-width: 400px;
    @media (max-width:767px) {
        width: 100%;
        min-width: 40px;
    }
    span{
        position: relative;
        z-index: 2;
    }
    &:before{
        content: '';
        position: absolute;
        height: 100%;
        width: 0;
        background-color: $atc-button-hover-bg-color;
        left: 0;
        top: 0;
        transition: 300ms;
        z-index: 1;
    }
    &:hover{
        color: $atc-button-hover-text-color;
        border: 1px solid $atc-button-hover-bg-color;
        &:before{
            width: 100%;
        }
    }
}

.button_search{
    background-color: transparent;
    font-size: 22px;
    float: right;
    padding: 0;
    width: 50px;
    border-radius: 0;
    height: 40px;
    min-width: inherit;
    @media (max-width:767px) {
        height: 40px;
    }
}

.button_atc{
    display: none;
    &.active{
        display: block;
    }
}
.button_width_half{
    margin: 0 3px;
    width: calc(50% - 6px);
    float: right;
    min-width: inherit;
}
.button_collection-list{
    background-color: #fe3466;
    color: #fff;
    line-height: 40px;
    position: relative;
    span{
        position: relative;
        z-index: 2;
    }
    &:before{
        content: '';
        position: absolute;
        height: 100%;
        width: 0;
        background-color: #111;
        left: 0;
        top: 0;
        transition: 300ms;
        z-index: 1;
    }
    &:hover{
        color: #fff;
        &:before{
            width: 100%;
        }
    }
}
.button_width_half-mobile{
    @media (max-width:767px) {
        margin: 0 3px;
        width: calc(50% - 6px);
        float: right;
        min-width: inherit;
    }
}

