.product-slider{
    .slick-track{
        display: flex;
    }
    .slick-slide {
        padding: 0 15px;
        height: auto;
        &>div{
            height: 100%;
        }
    }
    .product-grid{
        margin-bottom: 0;
        // padding-bottom: 70px;
        @media(max-width:767px){
            margin-bottom: 0;
        }
    }
    .slick-list{
        width: 100%;
        float: left;
    }
    // .product-grid__image {
    //     @media(max-width:767px){
    //         height: 240px;
    //     }
    // }
    .product-grid__atc-form-wrapper {
        bottom: 0;
    }
}

.product-slider__multiple-item-wrapper {
    width: 100%;
    float: left;
}
.product-slider__item-wrapper {
    width: 100%;
    float: left;
}