.search {
    position: absolute;
    width: 100%;
    max-width: 400px;
    right: 0;
    bottom: 0;
    z-index: -1;
    background-color: #fff;
    transition: 500ms;
    box-shadow: 0 0 5px rgba(0,0,0,.2);
    &.search__show{
        transform: translateY(100%);
    }
}
.search__input {
    background: no-repeat;
    border: none;
    height: 40px;
    padding: 0 15px;
    width: calc(100% - 90px);
    float: left;
    &:focus{
        outline: none;
    }
}
.search__bar{
    box-shadow: 0 0 5px rgba(0,0,0,.2);
    width: 100%;
    float: left;
    margin-bottom: 30px;
}
.search__result {
    padding-top: 30px;
}