.usp{
    border-bottom: 1px solid $color-border;
    text-align: center;
    @media(max-width: 991px){
        border: none;
        padding-bottom: 10px;
    }
}
.usp__item{
    display: inline-block;
    width: fit-content;
    white-space: nowrap;
    padding: 0 25px;
    line-height: 60px;
    margin-bottom: -5px;
    @media(max-width: 991px){
        line-height: 25px;
        text-align: center;
        padding-bottom: 10px;
    }
}
.usp__icon {
    font-size: 25px;
    float: left;
    width: 40px;
    text-align: left;
    @media(max-width: 991px){
        float: none;
        display: inline-block;
        font-size: 20px;
        width: 25px;
        // display: none;
        i{
            display: block;
            margin-bottom: -4px;
            float: left;
            font-size: 0;
        }
    }
}
.usp__text{
    width: calc(100% - 40px);
    float: left;
    @media(max-width: 991px){
        float: none;
        display: inline-block;
        width: auto;
    }
}

.usp{
    .slick-dots{
        height: 10px;
        margin-top: -15px;
        li{
            padding: 0 3px;
            font-size: 0;
            &.slick-active{
                button{
                    background-color: #7df4f4;
                }
            }
        }
        button{
            width: 7px;
            height: 7px;
            border-radius: 50%;
            background-color: #ccc;
            margin: 0;
            padding: 0;
            border: none;
        }
    }
}