.blog__item {
    margin-bottom: 35px;
}
.blog__title-url{
    color: $color-body-text;
    font-family: $font-header;
    font-size: 20px;
    text-transform: uppercase;
    letter-spacing: 1px;
    line-height: 24px;
}
.blog-post__title{
    font-size: 20px;
    color: $color-body-text;
    font-family: $font-header;
    font-weight: 400;
    line-height: 25px;
    margin: 15px 0 10px;
}
.blog-post__url{
    &:hover{
        text-decoration: none;
    }
}
.blog-post__view-all{
    padding: 50px 0;
    text-align: center;
}
.blog__read-more {
    margin-top: 20px;
}
h3.blog__title {
    margin-top: 15px;
    line-height: 20px;
}
.blog-post__item{
    @media(max-width:767px){
        margin-bottom: 25px;
    }
}