
.my-variant{
    display: none;
}
.product-single__thumbs{
    margin-top: 10px;
    padding: 0 30px;
    max-height: 200px;
    @media (max-width:767px) {
        padding: 0;
        max-height: 1000px;
    }
}
.product-single__thumb{
    padding: 0 5px;
    // max-height: 90px;
    max-width: 115px;
    overflow: hidden;
    &.active{
        border: 1px solid $color-border;
    }

    @media (max-width:767px) {
        max-width: 100%;
        padding: 0;
    }
}
.product-single__atc-btn{
    display: none;
    &.active{
        display: block;
    }
}
.product-single__vendor{
    font-size: 20px;
    font-weight: 700;
    text-transform: uppercase;
    letter-spacing: 1px;
}
.product-single__title{
    font-size: 30px;
    font-weight: 400;
    font-family: $font-body;
    margin-bottom: 0;
    padding-right: 100px;
    @media (max-width:767px) {
        font-size: 25px;
        line-height: 24px;
        font-weight: 500;
    }
}

.product-single__price{
    font-family: $font-body;
    font-size: 18px;
    margin-bottom: 15px;
    @media (max-width:767px) {
        padding-top: 10px;
    }
}
.product-single__price_main{
    font-weight: 700;
}
.product-single__price_compare {
    font-size: 18px;
    color: #a09e9e ;
}
.product-single__atc-btn{
    background-color: #111;
    color: #fff;
    // width: 90%;
    text-align: center;
    font-size: 20px;
    font-family: $font-subheader;
    letter-spacing: 1px;
    @media(max-width:767px){
        width: 100%;
    }
}
.product-single__atc {
    width: 100%;
    float: left;
    margin-top: 20px;
}
.product-single__description {
    width: 100%;
    float: left;
    padding-bottom: 25px;
}
// .product-single__image {
//     max-height: 500px;
// }
.product-single__info {
    width: 100%;
    float: left;
    position: relative;
    @media(max-width:767px){
        margin-top: 0;
    }
}
.product-single__thumb-src{
    &:focus{
        outline: none;
    }
}
.product-single__qty{
    label{
        display: inline-block;
        padding-right: 5px;
    }
    input{
        max-width: 320px;
    }
}
.product-single__content{
    width: 100%;
    overflow: hidden;
}
.product-single__usp {
    width: 100%;
    margin-top: 20px;
    float: left;
}
.product-single__usp-item {
    text-align: center;
}
.product-single__usp-icon {
    font-size: 30px;
}





// Modifiers
.product-single__thumb-arrow{
    position: absolute;
    top: 50%;
    left: 0;
    transform: translateY(-50%);
    font-size: 30px;
    z-index: 99;
    cursor: pointer;
}
.product-single__thumb-arrow_right{
    left: inherit;
    right: 0;
}
.product-single__image{
    @media(max-width:767px){
        pointer-events: none;
        display: none;
    }
}


.product-single__bottom-info{
    width: 70%;
    @media(max-width:991px){
        width: 100%;
    }
}
