.cart {
    letter-spacing: 1px;
}
.cart__item-header, .cart__item, .cart__total-summary{
    display: flex;
    flex-wrap: wrap;
    flex: 0 20%;
    list-style: none;
    margin: 0;
    padding: 0;
    li{
        border-bottom: 1px solid $color-border;
        width: calc(100% / 6);
        padding: 10px;
    }
}
.cart__item-header{
    li{
        font-weight: 700;
        text-transform: uppercase;
    }
}
.cart__total-summary{
    li{
        border-bottom: none;
    }
}
.cart__amount, .cart__qty, .cart__remove, .cart__subtotal-text, .cart__subtotal{
    text-align: center;
    justify-content: center;
}
.cart__item{
    li{
        display: flex;
        border-bottom: 1px solid $color-border;
        align-items: center;
    }
}
.cart__url {
    max-width: 90px;
}
.cart__qty{
    input{
        max-width: 70px;
        text-align: center;
    }
}
.cart__item-name, .cart__item-remove{
    color: $color-body-text;
    &:hover{
        color: $color-body-text;
        text-decoration: none;
    }
}
.cart__subtotal-text, .cart__subtotal{
    font-weight: 700;
}
.cart__bottom{
    // padding-right: 75px;
    margin-top: 15px;
}



// Modifiers
.cart__bottom_align_right{
    text-align: right;
}
.cart__bottom_align_center{
    text-align: center;
}
.cart__bottom_align_left{
    text-align: left;
}




@media(max-width:767px){
    .cart__item-header {
        display: none;
    }
    
    .cart__item {
        display: block;
        width: 100%;
        float: left;
        border-top: 1px solid $color-border;
        padding: 10px 0;
        li{
            display: block;
            border: none;
            padding: 5px 10px;
            position: relative;
        }
        .cart__image {
            width: 45%;
            float: left;
        }
        .cart__description, .cart__amount, .cart__qty, .cart__remove, .cart__item-totle{
            width: 55%;
            float: right;
            text-align: left;
        }
    }
    .cart__total-summary{
        display: block;
        width: 100%;
        float: left;
        border-bottom: 2px solid $color-border;
        border-top: 2px solid $color-border;
        text-align: center;

        li{
            display: inline-block;
            width: auto;
            text-transform: uppercase;
            padding: 15px 0;
            font-size: 16px;
            &.cart__blank{
                display: none;
            }
        }
    }
    .cart__item-remove{
        &:before{
            content: 'Remove';
            text-transform: uppercase;
            padding-right: 40px;
        }
    }
    .cart__qty, .cart__item-totle{
        &:before{
            content: 'Qty';
            text-transform: uppercase;
            padding-right: 40px;
        }
    }
    .cart__item-totle{
        &:before{
            content: 'Total price';
        }
    }
    .cart__bottom {
        width: 100%;
        float: left;
        padding: 0;
        text-align: center;
        margin-bottom: 30px;
    }
}