.collections{
    width: 100%;
    overflow-x: scroll;
}
.collections__items {
    width: max-content;
}
.collections__item {
    width: 85px;
    float: left;
    padding: 0 5px;
}
.collections__image {
    height: 60px;
    width: 60px;
    border-radius: 50%;
    overflow: hidden;
    margin: 10px auto 5px auto;
    border: 1px solid $color-border;
}
.collections__url{
    display: block;
    color: $color-body-text;
    font-size: 12px;
    line-height: 16px;
    padding-bottom: 10px;
    text-transform: uppercase;
    text-align: center;
}

.collection__hero{
    // margin-top: -30px;
    margin-bottom: 80px;
    padding-bottom: 5px;
    border-bottom: 1px solid $color-border;
}
.collection__hero-image_desktop{
    @media(max-width:991px){
        display: none;
    }
}
.collection__hero-image_tab{
    display: none;
    @media(max-width:991px){
        display: block;
    }
    @media(max-width:767px){
        display: none;
    }
}
.collection__hero-image_mobile{
    display: none;
    @media(max-width:767px){
        display: block;
    }
}
.collection__top{    
    margin-bottom: 60px;
}
.collection__pagination-info{
    text-transform: uppercase;
    padding-bottom: 3px;
}


.collection__filter-title {
    font-size: 15px;
    text-transform: uppercase;
    letter-spacing: 1px;
    margin-bottom: 10px;
    font-weight: 700;
    border-bottom: 1px solid $color-border;
}
a.collection__filter-name{
    color: $color-body-text;
    &:hover{
        color: $color-body-text;
    }
}
.collection__filter-group {
    margin-bottom: 15px;
    // padding-bottom: 20px;
    // border-bottom: 1px solid #eee;
}

.collection__filter-mob{
    display: none;
}
.collection__filter {
    padding-right: 10px;
    @media(max-width:767px){
        padding-right: 0;
    }
}



@media(max-width:767px){
    .collection__filter {
        max-height: 0;
        overflow: hidden;
        transition: 500ms;
        &.open{
            max-height: 10000px;
        }
    }
.collection__filter-mob{
    display: block;
    position: relative;
    font-size: 18px;
    background-color: #111;
    line-height: 40px;
    color: #fff;
    padding: 0 15px;
    margin-bottom: 15px;
    &::after{
        content: "\e994";
        font-family: icomoon!important;
        position: absolute;
        right: 15px;
        top: 0;
    }
}
}