.product-grid {
    text-align: center;
    width: 100%;
    float: left;
    height: 100%;
    padding-bottom: 65px;;
    position: relative;
    a{
        color: $color-body-text;
        text-decoration: none;
        &:hover{
            text-decoration: none;
        }
    }
}
.product-grid__image {
    // height: $grid-image-height;
    overflow: hidden;
    position: relative;
    border: 1px solid $color-border;
    // img{
    //     position: absolute;
    //     width: 100%;
    //     left: 0;
    //     top: 50%;
    //     transform: translateY(-50%);
    // }
    &:before{
        content: '';
        position: absolute;
        width: 100%;
        height: 100%;
        background-color: rgba(255,255,255,.3);
        left: 0;
        top: 0;
        transition: 500ms;
        opacity: 0;
    }
    &:hover{
        .product-grid__img_alt{
            opacity: 1;
        }
        &:before{
            opacity: 1;
        }
    }
}
.product-grid__img_alt{
    position: absolute;
    left: 0;
    top: 0;
    z-index: 9;
    width: 100%;
    opacity: 0;
    transition: 300ms;
}
.product-grid__img_swatch{
    position: absolute;
    left: 50%;
    top: 50%;
    height: 100%;
    width: auto;
    max-width: inherit;
    transform: translate(-50%, -50%);
    opacity: 0;
    transition: 300ms;
}
