//         opacity: 1;
//     }
// }
.product-grid__title{
    font-size: 14px;
    color: $color-body-text;
    text-transform: uppercase;
    letter-spacing: 1px;
    margin-top: 20px;
    line-height: 18px;
    @media(max-width:767px){
        line-height: 20px;
        letter-spacing: 0;
    }
}
.product-grid__vendor{
    font-size: 14px;
    color: $color-body-text;
    text-transform: uppercase;
    letter-spacing: 1px;
    margin-top: 15px;
    line-height: 18px;
    margin-bottom: -12px;
    @media(max-width:767px){
        line-height: 20px;
        letter-spacing: 0;
    }
}
.product-grid__sale-price{
    color: #e32c2b;
}
.product-grid__price {
    font-size: 24px;
    font-weight: 600;
}
.product-grid__slider-arrow{
    position: absolute;
    top: $grid-image-half-height;
    left: 0;
    transform: translate(-50px, -50%);
    font-size: 50px;
    cursor: pointer;
    z-index: 9;
    @media(max-width:991px){
        transform: translate(-20px, -50%);
        font-size: 35px;
        top: 120px;
    }
    @media(max-width:767px){
        transform: translate(10px, -50%);
    }
}
.product-grid__slider-arrow_right{
    left: inherit;
    right: 0;
    transform: translate(50px, -50%);
    @media(max-width:991px){
        transform: translate(20px, -50%);
    }
    @media(max-width:767px){
        transform: translate(-10px, -50%);
    }
}

.product-grid__url {
    width: 100%;
    float: left;
    display: block;
    &:focus{
        outline: none;
        border: none;
    }
}

.product-grid__atc-form-wrapper {
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    bottom: 0;
}
.product-grid__compare-price{
    color: #a09e9e;
    font-size: 18px;
}

