.product-grid__variant {
    margin-top: 15px;
}
.product-grid__swatch{
    ul{
        list-style: none;
        padding: 0;
        margin: 0;
    }
}
.product-grid__swatch-item{
    display: inline-block;
}
.product-grid__swatch-color {
    width: 20px;
    height: 20px;
    display: block;
    border-radius: 50%;
    margin: 0 2px;
    border: 1px solid $color-border;
}
.product__collection-item{
    margin-bottom: $grid-bottom-spacing;
}
.product-grid__review {
    margin-top: 8px;
}

