
.footer {
    padding: 35px 0;
}
.footer__nav {
    list-style: none;
    padding: 0;
    margin: 0 0 15px 0;
}
.footer__nav-item {
    display: inline-block;
}
.footer__nav-url {
    padding: 0 10px;
    // text-transform: uppercase;
    font-size: 14px;
    // font-weight: 600;
    letter-spacing: 1px;
}
.footer__payment-icons {
    margin-top: 15px;
}
.footer__payment-items {
    margin: 0;
    padding: 0;
    list-style: none;
}
.footer__payment-item {
    display: inline-block;
    margin-bottom: 5px;
}
.footer__item-title{
    text-transform: uppercase;
    letter-spacing: 1px;
}

.footer__logo {
    max-width: 150px;
}






// Modifier
.footer_bg_dark{
    background-color: #111;
    color: #fff;
}
.footer_align_center{
    text-align: center;
}
.footer__nav-url_current{
    color: $color-accent !important;
}
.footer__item_multiple{
    text-align-last: left;
    margin-bottom: 35px;
    .footer__nav-item{
        display: block;
    }
    .footer__nav-url{
        padding: 5px 0;
    }
}

.footer_text-left{
    text-align: left;
}

@media(max-width:767px){
    .footer_column-row{
        display: block;
    }
    .footer__column{
        width: 100%;
        float: left;
    }
    .footer__item-title{
        position: relative;
        &:after{
            content: "\e9ba";
            position: absolute;
            font-family: icomoon!important;
            right: 0;
            top: 2px;
        }
        &.accordion__title_active{
            &:after{
                content: "\e99d";
            }
        }
    }
    .footer__nav {
        max-height: 0;
        overflow: hidden;
    }
    .footer__item_multiple {
        margin-bottom: 0;
    }
    .footer__column_subscription{
        padding-bottom: 20px;
    }
}