.nav {
    padding: 0;
    margin: 0;
    list-style: none;
    display: inline-block;

    &>.nav__item> {
        .nav__url {
            line-height: $nav-height;
            display: block;
            color: $color-nav-link;            
            font-size: 14px;
            font-weight: 600;
            letter-spacing: 1px;
            &.nav__url_current{
                color: $color-accent;
            }
            @media(max-width: 991px){
                line-height: 60px;
            }
        }
    }
    &>.nav__item{
        transition: 300ms;
        @media(max-width: 991px){
            position: relative;
        }
        &:hover{
            .nav__url{
                color: $nav-bg;
            }
        }
    }
}

.nav__item {
    display: inline-block;
    color: $color-header-link;

    @media(max-width:991px){
        width: 100%;
        float: left;
        .nav__url_shop{
            display: none !important;
        }
    }

    &:hover {
        .nav__sub {
            visibility: visible;
            opacity: 1;
            z-index: 99;
        }
    }
}

.nav__sub {
    visibility: hidden;
    position: absolute;
    padding: 10px 0;
    background-color: #fff;
    width: 200px;
    opacity: 0;
    transition: 300ms;
    z-index: -1;
    box-shadow: 0 2px 3px rgba(0,0,0,.2);
    @media(max-width: 991px){
        z-index: 99;
    }

    .nav__item {
        display: block;

        &:last-child {
            border: none;
        }
    }

    .nav__url {
        padding: 5px 15px;
        text-transform: none;
        display: block;
        text-align: left;
    }
}

.nav__url {
    padding: 0 15px;
    text-transform: uppercase;
    font-size: $header-font-size;
    transition: 300ms;
    position: relative;
    color: $color-header-link;

    &:hover {
        text-decoration: none;
        color: $color-accent !important;
    }
}


.nav__url_current{
    color: $color-accent;
}


.nav_main {
    // background-color: $nav-bg;
    position: absolute;
    width: 100%;
    top: 50%;
    transform: translateY(-50%);
    left: 0;
    @media(max-width: 991px){
        margin-left: 0;
        width: 100%;
        background-color:  $color-header-bg;
        left: 0;
        top: 43px;
        max-height: 0;
        overflow: hidden;
        transition: max-height 1s ease-in-out -600ms;

        .nav__item {
            display: block;
            &:last-child{
                margin-bottom: 15px;
            }
        }

        &.open{
            max-height: 550px;
            overflow-y: scroll;
            transition: max-height 1s ease-in-out 0s;
        }
    }
}

.nav_e-commerce {
    float: right;
    position: relative;
    z-index: 10;
    @media(max-width: 991px){
        width: 150px;
        text-align: right;
        .nav__item{
            width: auto;
            float: none;
        }
    }
}
.nav__search-icon{
    padding: 0 10px;
    cursor: pointer;
    transition: 300ms;
    @media(max-width: 991px){
        padding: 0;
    }
}

.nav__cart-count {
    position: absolute;
    font-size: 12px;
    right: -5px;
    top: 25px;
    line-height: 15px;
    width: 15px;
    height: 15px;
    border-radius: 50%;
    text-align: center;
    font-weight: 700;
    color: $color-body-text;
    @media(max-width: 991px){
        top: 10px;
    }
}

.nav__icon {
    font-size: 20px !important;
    &:hover{
        color: $color-accent;
    }
}
.nav__url_current{
    color: $color-accent;
}
.hamburger {
    width: 40px;
    font-size: 25px;
    line-height: 25px;
    position: absolute;
    z-index: 10;
    top: 50%;
    left: 15px;
    transform: translateY(-50%);
    @media (max-width: 991px) {
        display: block;
    }
}

// Mega menu
.nav__mega-menu {
    width: 100%;
    left: 0;
    display: flex;
    flex-flow: wrap;
    box-shadow: 0 3px 3px rgba(0, 0, 0, .1);
    position: absolute;
    background-color: #fff;
    padding: 30px 5%;
    .nav__item{
        flex-grow: 1;
        flex-basis: 0;
        display: inline-block;
        padding: 5px;
        text-align: left;
        @media (max-width: 991px) {
            width: 90px;
        }
        .nav__sub {
            visibility: visible;
            position: relative;
            width: 100%;
            background-color: #fff;
            display: flex;
            flex-flow: wrap;
            .nav__sub_half {
                width: 50%;
            }
        }
        .nav__item{
            display: block;
            padding: 5px 0;
            .nav__url:hover{
                color: #fe3466;
            }
        }
    }
    .nav__url{
        font-size: 12px;
        text-transform: uppercase;
        line-height: 16px;
        padding: 3px 0 0;
        color: $color-body-text;
    }

    @media(max-width: 991px){
        width: 100%;
        position: relative;
        padding: 0 15px;
        visibility: visible;
        opacity: 1;
        max-height: 0;
        overflow: hidden;
        border: none;
    }

    &> .nav__item >{
        .nav__url{
            font-weight: 700;
        }
    }

}
.nav__meage-menu-image {
    margin-bottom: 7px;
    background-color: rgba(255,255,255,.5);
    @media (max-width: 991px) {
        width: 80px;
        height: 80px;
        border-radius: 50%;
        overflow: hidden;
    }
}
.hamburger {
    color: $color-header-bg;
}
.nav__search {
    overflow: hidden;
    background-color: #fff;
    width: calc(100% - 30px);
    margin-left: 15px;
    margin-top: 12px;
    margin-bottom: 10px;
    .search__input {
        height: 40px;
        width: calc(100% - 70px);
    }
    .button_search {
        width: 70px;
        line-height: 40px;
    }
}


// Modifire
.nav_center{
    text-align: center;
    width: 100%;
    @media(max-width: 991px){
        top: 0;
        z-index: 9;
    }
}
.nav__sub_open{
    max-height: 1000px;
    @media(max-width: 991px){
        max-height: 265px;
        overflow-y: scroll;
    }
}
.nav__url_cart {
    padding: 0;
    &:hover{
        .nav__cart-count{
            color: $color-body-text !important;
        }
    }
}