.viable-section{
    padding: $section-padding 0;
    @media (max-width:767px) {
        padding: 25px 0;
    }
}
.viable-section__title {
    margin-bottom: 50px;
    @media (max-width:767px) {
        margin-bottom: 25px;
    }
    h2{
        font-size: 55px;
        font-weight: 400;
        position: relative;
        text-transform: uppercase;
        margin: 0;
        font-family: $font-header;
        letter-spacing: 1px;
        font-weight: inherit;
        @media (max-width:1920px) {
            font-size: 32px;
            line-height: 42px;
        }
        @media (max-width:767px) {
            font-size: 25px;
            line-height: 30px;
        }
    }
    h5{
        font-family: $font-header;
        font-size: 23px;
        line-height: 28px;
        // text-transform: uppercase;
        letter-spacing: 1px;
        font-weight: inherit;
        @media (max-width:1920px) {
            font-size: 20px;
            line-height: 23px;
            margin-top: 5px;
        }
        @media (max-width:767px) {
            font-size: 16px;
            line-height: 20px;
            margin-top: 10px;
        }
    }
}



// Modifiers
.viable-section__title_align_center {
    text-align: center;
}
.viable-section__title_align_left {
    text-align: left;
}
.viable-section__title_align_right {
    text-align: right;
}