
.site-header{
    position: relative;
    z-index: 99;
}
.header-wrapper{
    width: 100%;
    position: relative;
}
.header {
    width: 100%;
    box-shadow: 0 0 1px rgba(0,0,0,0.2);
}
.header__top{
    height: $header-height;
    position: relative;
    padding: 0 $fluid-width-padding;
    background-color: #cbe4de;
    @media(max-width: 991px){
        height: 60px;
        padding: 0 15px;
    }
}
.header__bottoom {
    width: 100%;
    float: left;
    position: relative;
}
.header__logo {
    display: inline-block;
    position: absolute;
    z-index: 9;
    top: 50%;
    transform: translateY(-50%);
    cursor: pointer;
    @media(max-width: 991px){
        width: 150px;
        left: 50%;
        transform: translate(-50%, -50%);
    }
    @media(max-width: 767px){
        width: 85px;
    }
}
.logo-image {
    color: #fff;
    font-weight: 700;
    font-size: 25px;
    display: inline-block;
    text-transform: uppercase;
    &:hover{
        color: #fff;
        text-decoration: none;
    }
}
.header__logo-img{
    float: left;
}


.logo-image{
    display: block;
}

.header__search {
    width: 100%;
    max-width: 440px;
    position: absolute;
    right: 0;
    bottom: 0;
    border: 1px solid $color-border;
    background-color: #fff;
    visibility: hidden;
    opacity: 0;
    transition: 300ms;
    @media(max-width: 991px){
        display: none;
    }
    &.search__show{
        visibility: visible;
        opacity: 1;
        transform: translateY(100%);
    }
}

.hamburger__text{
    font-size: 10px;
    text-transform: uppercase;
    line-height: 10px;
    text-align: center;
    float: left;
    margin-top: -8px;
    margin-left: -2px;
}
.header-wrapper_fixed{
    position: fixed;
    top: 0;
    left: 0;
    z-index: 99;
}