.hero {
    margin: 0 0 -6px 0;
    width: 100%;
    position: relative;
    z-index: -1;
    @media(max-width:991px){
        min-height: 170px;
    }
    @media (max-width:767px) {
        min-height: 150px;
    }
}
.hero__item {
    max-height: 720px;
    overflow: hidden;
    position: relative;
    width: 100%;
    @media (max-width:767px) {
        text-align: center;
    }
}
.hero__content {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    width: 50%;
    padding: 0 50px;
    @media(max-width:991px){
        padding: 0 35px;
    }
    @media (max-width:767px) {
        top: 0;
        position: relative;
        width: 100%;
        padding: 30px 15px;
        transform: inherit;
        float: left;
    }
}
.hero__title {
    font-size: 55px;
    color: #fff;
    font-weight: 500;
    text-transform: uppercase;
    font-family: $font-header;
    margin: 0;
    letter-spacing: 1px;
    @media (max-width:1919px) {
        font-size: 35px;
        line-height: 43px;
    }
    @media(max-width:991px){
        font-size: 30px;
        line-height: 35px;
    }
    @media (max-width:767px) {
        font-size: 25px;
        line-height: 30px;
        color: $color-body-text !important;
    }
}
.hero__subtitle {
    color: #fff;
    font-family: $font-subheader;
    font-size: 25px;
    text-transform: uppercase;
    letter-spacing: 1px;
    line-height: 30px;
    margin: 10px 0 20px;
    @media (max-width:1919px) {
        font-size: 18px;
        line-height: 26px;
    }
    @media(max-width:991px){
        font-size: 16px;
        line-height: 22px;
    }
    @media (max-width:767px) {
        line-height: 20px;
        color: $color-body-text !important;
    }
}
.hero__url{
    margin-top: 25px;
    min-width: 150px;
    @media(max-width:991px){
        margin-top: 10px;
    }
    &:hover{
        text-decoration: none;
    }
}
.hero__slider-arrow {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    z-index: 5;
    font-size: 35px;
    width: 50px;
    height: 50px;
    border-radius: 50%;
    text-align: center;
    background-color: rgba(255,255,255,.5);
    padding-top: 3px;
    cursor: pointer;
    transition: 300ms;
    opacity: .5;
    &:hover{
        background-color: rgba(255,255,255,.9);
        opacity: 1;
    }
}
.hero-fluid{
    padding: 0 15px;
}


// Modifires
.hero__content_left{
    left: 0;
}
.hero__content_center{
    left: 50%;
    transform: translate(-50%, -50%);
    text-align: center;
}
.hero__content_right{
    right: 0;
}
.hero__slider-arrow_left{
    left: 30px;
}
.hero__slider-arrow_right{
    right: 30px;
}
.hero__image_left{
    float: left;
    max-width: 50% !important;
    @media (max-width:767px) {
        max-width: 100% !important;
        display: none !important;
        &.hero__image_image-with-text{
            display: block !important;
        }
    }
}
.hero__image_center{
    margin: 0 auto;
    display: block;
    width: 100%;
    // max-width: 1920px;
    @media (max-width:767px) {
        display: none !important;
        &.hero__image_image-with-text{
            display: block !important;
        }
    }
}
.hero__image_right{
    float: right;
    max-width: 50% !important;
    @media (max-width:767px) {
        max-width: 100% !important;
        display: none !important;
        &.hero__image_image-with-text{
            display: block !important;
        }
    }
}
.container-fluid{
    .hero__content_left{
        max-width: 700px;
        left: 50%;
        transform: translate(-100%, -50%);        
        @media (max-width:767px) {
            left: 0;
            transform: inherit;
            &.hero__content_mobile-align_middle{
                transform: translateY(-50%);
            }
        }
    }
    .hero__content_right{
        max-width: 700px;
        right: 50%;
        transform: translate(100%, -50%);
        @media (max-width:767px) {
            right: 0;
            transform: inherit;
            &.hero__content_mobile-align_middle{
                transform: translateY(-50%);
            }
        }
    }
}
.slick-slide{
    .hero__image_mobile{
        display: none;
        @media (max-width:767px) {
            display: block;
        }
    }
}

.slick-list {
    width: 100%;
}

@media (max-width:767px) {
    .hero__content_mobile-align_middle{
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
    }
    .hero__item_height-mobile_full{
        max-height: calc(100vh - 150px);
    }
}