
.tab{
    margin-bottom: 25px;
}
.tab__item {
    display: inline-block;
    line-height: 35px;
    padding: 0 15px;
    position: relative;
    cursor: pointer;
}
.tab__content{
    width: 100%;
    visibility: hidden;
    opacity: 0;
    transition: 300ms;
    .slick-list{
        height: 0;
        overflow: hidden;
    }
}

// Modifier
.tab_align_center{
    text-align: center;
}
.tab__content_active{
    visibility: visible;
    opacity: 1;
    .slick-list{
        height: auto;
    }
}
.tab__item_active{    
    &:after{
        content: '';
        width: calc(100% - 30px);
        bottom: 0;
        position: absolute;
        border-bottom: 2px solid $color-border;
        left: 15px;
    }
}