.menu-mobile{
    width: 80%;
    position: fixed;
    top: 0;
    left: 0;
    height: 100vh;
    overflow-x: hidden;
    overflow-y: scroll;
    background-color: #fff;
    z-index: 99;
    padding: 0;
    margin: 0;
    list-style: none;
    visibility: hidden;
    transition: 700ms;
    transform: translateX(-101%);
    box-shadow: 0 0 5px rgba(0,0,0,.2);

    @media(min-width: 992px){        
        display: none;
    }
}

.menu-mobile__sub{
    list-style: none;
    padding: 0;
    margin: 0;
}
.menu-mobile__mega-menu{
    position: absolute;
    background-color: #fff;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    z-index: 9;
    transition: 700ms;
    transform: translateX(101%);
    visibility: hidden;
}
.menu-mobile__sub_sub{
    list-style: none;
    padding: 0;
    margin: 0;
    position: absolute;
    background-color: #fff;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    z-index: 10;
    transition: 700ms;
    transform: translateX(101%);
    visibility: hidden;
}


.menu-mobile__item {
    display: block;
}
.menu-mobile__url {
    display: block;
    line-height: 40px;
    padding: 0 15px;
    color: $color-body-text;
    transition: 300ms;
    &:hover{
        color: #fe3466;
        background-color: rgba(0,0,0,.03);
    }
}
.menu-mobile__back {
    line-height: 40px;
    padding: 0 15px;
}

.menu-mobile__item_dropdown{
    .menu-mobile__url {
        i{
            position: absolute;
            right: 15px;
            line-height: 40px;
            font-size: 18px;
        }
    }
}

.menu-mobile__item_dropdown_open{
    & > .menu-mobile__sub_sub{
        visibility: visible;
        transform: inherit;
    }
    & > .menu-mobile__mega-menu{
        visibility: visible;
        transform: inherit;
    }
}
.menu-mobile__item_image {
    margin-top: 25px;
    padding: 0 15px;
}
.menu-mobile__backdrop{
    width: 100%;
    position: fixed;
    top: 0;
    left: 0;
    height: 100vh;
    background-color: rgba(255,255,255,.7);
    z-index: 98;
    visibility: hidden;
    opacity: 0;
    transition: 700ms;
}
.body-menu-mobile_open{
    .menu-mobile{
        visibility: visible;
        transform: none;
    }
    .menu-mobile__backdrop{
        visibility: visible;
        opacity: 1;
        i {
            position: absolute;
            right: 15px;
            top: 15px;
            font-size: 40px;
        }
    }
}

.menu-mobile__search {
    border: 1px solid $color-border;
    overflow: hidden;
    margin-bottom: 10px;
}
.menu-mobile__back{
    transition: 300ms;
    &:hover{
        background-color: rgba(0,0,0,.03);
    }
}