.pagination{
    line-height: 40px;
    margin-top: 30px;
    text-align: center;
    margin-bottom: 50px;
    .page{
        display: inline-block;
        margin: 0 1px;
        cursor: pointer;
        a{
            color: $color-body-text;
            padding: 0 15px;
            &:hover{
                text-decoration: none;
                color: $color-body-text;
            }
        }
        &.current{
            font-weight: 700;
        }
    }
    .next, .prev{
        display: none;
    }

    .pagination__prev, .pagination__next{
        color: $color-body-text;
        text-transform: uppercase;
        &:hover{
            text-decoration: none;
            color: $color-body-text;
        }
    }

}