.accordion__title {
    line-height: 60px;
    padding: 0 5px;
    border-top: 1px solid $color-border;
    border-bottom: 1px solid $color-border;
    margin: -1px 0 0 0;
    font-family: $font-header;
    font-size: 16px;
    text-transform: uppercase;
    letter-spacing: 1px;
    font-weight: 400;
    position: relative;
    cursor: pointer;
    &:after {
        content: '\002B';
        font-weight: bold;
        float: right;
        margin-left: 5px;
      }
      &.accordion__title_active{
          &:after{
            content: "\2212";
          }
      }
}
.accordion__panel {
    max-height: 0;
    overflow: hidden;
    transition: max-height 1s ease-in-out -600ms;

}
.accordion__panel_active{
    max-height: 5000px;
    transition: max-height 1s ease-in-out 0s;
}
.accordion__panel-text{
    padding: 10px 5px;
    video{
        max-width: 100%;
    }
}
.accordion__panel-video{
    width: 100%;
    height: 465px;
    @media(max-width: 1440px){
        height: 310px;
    }
}


@media(max-width: 767px){
    .accordion__title {
        line-height: 40px;
        padding: 10px 25px 10px 10px;
        font-size: 14px;
    }
    .accordion__title:after {
        position: absolute;
        right: 10px;
        top: 50%;
        transform: translateY(-50%);
    }
}
.accordion__panel-text-title {
    font-weight: 700;
    margin: 10px 0;
}

.accordion__panel_tech-row{
    margin-bottom: 10px;
}