.filter{
    padding: 10px 0;
    margin: 0;
    list-style: none;
}
.filter__item{
    display: inline-block;
    width: 100%;
    max-width: 200px;
    position: relative;    
    margin-left: 5px;
    &:after{
        content: "\e92e";
        position: absolute;
        right: 10px;
        top: 5px;
        font-family: icomoon !important;
        width: 15px;
        height: 15px;
        font-size: 18px;
        z-index: 21;
    }
    @media(max-width: 767px){
        max-width: inherit;
        margin: 0;
    }
}
.filter__item-select {
    width: 100%;
    height: 35px;
    appearance: none;
    border: 1px solid $color-border;
    padding: 0 10px;
    float: left;
}


// modifires
.filter_aligen_right{
    text-align: right;
}
.filter__option_color{
    width: 100%;
    height: 35px;
    border: 1px solid $color-border;
    padding: 0 10px;
    text-align: left;
    float: left;
    ul{
        padding: 3px 10px;
        margin: 0;
        position: absolute;
        width: calc(100% - 2px);
        left: 1px;
        z-index: 20;
        li{
            display: none;
            padding: 3px 0;
            cursor: pointer;
            transition: 300ms;
            text-align: left;
            &.selected{
                display: block;
            }
        }
        &.open{
            background-color: #fff;
            li{
                display: block;
                &:hover{
                    background-color: rgba(0,0,0,.05);
                }
            }
        }
    }
}
.filter__option_color-swatch{
    width: 20px;
    height: 20px;
    border-radius: 50%;
    float: left;
    margin-right: 5px;
    border: 1px solid #eee;
}