.text-columns__top-image{
    margin-bottom: 20px;
}
.text-columns__top-img {
    max-width: 400px;
}

.text-columns__item{
    text-align: center;
    @media (max-width: 767px) {
        width: 100%;
    }
}
.text-columns__title{
    font-size: 22px;
    text-transform: uppercase;
    margin-bottom: 10px;
    font-family: $font-header;
    letter-spacing: 1px;
}
.text-columns__usp{
    max-width: 800px;
    margin: 0 auto;
}
.text-columns__usp-image {
    width: 70px;
    margin: 0 auto;
}
.text-columns__usp-text {
    text-align: center;
    padding: 10px;
    line-height: 18px;
}
.text-columns__usp-row{
    margin-top: $section-padding;
}


// Modifires
.text-columns__top-image_align_center {
    text-align: center;
}