.shipping-bar {
    padding: 0 $shipping-bar-padding;
    font-size: $shipping-font-size;
    text-align: center;
    text-transform: uppercase;
    background-color: $color-shipping-bg;
    color: $color-shipping-text;
}
.shipping-bar_fixed{
    position: fixed;
    top: $shipping-top;
    left: 0;
    width: 100%;
    z-index: 99;
}
.shipping-bar__goal{
    color: $color-accent;
}