.page__title {
    margin-bottom: 25px;
    @media (max-width:767px) {
        margin-bottom: 30px;
    }
    h1{
        font-size: 45px;
        line-height: 62px;
        font-weight: 500;
        position: relative;
        text-transform: uppercase;
        margin: 0;
        font-family: $font-header;
        letter-spacing: 1px;
        font-weight: inherit;
        @media (max-width:1919px) {
            font-size: 25px;
            line-height: 42px;
        }
        @media (max-width:767px) {
            font-size: 25px;
            line-height: 30px;
        }
    }
    h5{
        font-family: $font-header;
        font-size: 25px;
        text-transform: uppercase;
        letter-spacing: 1px;
        font-weight: inherit;
        margin-bottom: 0;
        margin-top: 10px;
        @media (max-width:1919px) {
            font-size: 18px;
        }
        @media (max-width:767px) {
            font-size: 14px;
            line-height: 18px;
        }
    }
}
.page__content{
    padding-bottom: 50px;
    min-height: calc(100vh - 400px);
    img{
        height: auto;
        width: auto;
    }
}
.page__text{
    margin-bottom: 50px;
}
.page__account-header{
    text-transform: uppercase;
    a{
        padding: 0 10px;
        position: relative;
        &:after{
            content: '';
            position: absolute;
            right: -3px;
            top: 3px;
            height: 15px;
            border-right: 1px solid $color-border;
        }
        &:last-child:after{
            display: none;
        }
    }
}

// Modifier
.page__title_align_center{
    text-align: center;
}
.page__title_align_left{
    text-align: left;
}
.page__title_align_right{
    text-align: right;
}