.variant__option {
    margin-bottom: 15px;
}
.variant__option-name {
    font-weight: 700;
    text-transform: uppercase;
    margin-bottom: 3px;
}
.variant__item {
    position: relative;
    display: inline-block;
}
.variant__input {
    opacity: 0;
    position: absolute;
    z-index: -1;
    &:checked + .variant__label{
        background-color: $atc-button-bg-color;
        color: $atc-button-text-color;
    }
}
.variant__label {
    margin-bottom: 5px;
    padding: 0 8px;
    border: 1px solid $color-border;
    line-height: 30px;
    cursor: pointer;
    position: relative;
    margin-right: 5px;
    min-width: 35px;
    text-align: center;
    font-weight: 700;
    text-transform: uppercase;
}

.variant__tooltip {
    position: absolute;
    z-index: -1;
    font-weight: 400;
    white-space: nowrap;
    background-color: #333;
    color: #fff;
    top: -10px;
    left: 50%;
    transform: translate(-50%, -100%);
    padding: 0 10px;
    box-shadow: 0 0 5px rgba(0,0,0,.3);
    opacity: 0;
    transition: 300ms;
    border-radius: 3px;
    text-transform: none;
    &:after{
        position: absolute;
        content: '';
        width: 15px;
        height: 15px;
        background-color: #333;
        box-shadow: 0 0 5px rgba(0,0,0,.3);
        left: 50%;
        bottom: 0;
        transform: rotate(45deg) translate(-30%, 50%);
        z-index: -1;
    }
}


// Modifires
.variant__label_color {
    width: 35px;
    height: 35px;
    border-radius: 50%;
    margin-right: 5px;
    border: 1px solid #eee;
    &:hover{
        .variant__tooltip{
            opacity: 1;
            z-index: 1;
        }
    }
}
.variant__input_color:checked + .variant__label_color{
    box-shadow: 0 0 5px rgba(0,0,0,.5);
}