.cart-drawer-wrapper{
    position: relative;
    z-index: 999;
}
.cart-drawer{
    width: 100%;
    max-width: 450px;
    position: fixed;
    background-color: #fff;
    z-index: 101;
    right: 0;
    padding: 30px;
    height: 100vh;
    top: 0;
    transform: translateX(100%);
    transition: 500ms;
    box-shadow: 0 0 5px rgba(0,0,0,.2);
    @media(max-width: 767px){
        padding: 15px;
    }
}
.cart-drawer__header {
    text-align: center;
    position: relative;
    padding-bottom: 10px;
    border-bottom: 1px solid $color-border;
}
.cart-drawer__header-info {
    font-weight: 700;
    text-transform: uppercase;
}
.cart-drawer__image {
    width: 85px;
    float: left;
}
.cart-drawer__close {
    position: absolute;
    top: -3px;
    right: 0;
    font-size: 20px;
    cursor: pointer;
}
.cart-drawer__item {
    position: relative;
    overflow: hidden;
    padding: 10px 0;
    border-bottom: 1px solid #222;
}
.cart-drawer__info {
    width: calc(100% - 85px);
    float: left;
    padding-right: 80px;
    padding-left: 10px;
}
.cart-drawer__price {
    position: absolute;
    right: 0;
    bottom: 15px;
}
.cart-drawer__remove {
    position: absolute;
    right: 0;
    top: 15px;
}
.cart-drawer__url{
    color: $color-body-text;
    &:hover{
        color: $color-body-text;
        text-decoration: none;
    }
}
.cart-drawer__qty-btn, .cart-drawer__qty-input{
    border: 1px solid $color-border;
    background: none;
    float: left;
    height: 25px;
    width: 25px;
    padding: 0;
    line-height: 25px;
}
.cart-drawer__qty-input{
    width: 40px;
    border-left: none;
    border-right: none;
    text-align: center;
}
.cart-drawer__qty {
    overflow: hidden;
    margin-top: 5px;
}
.cart-drawer__remove-url {
    width: 17px;
    height: 17px;
    padding: 1px 0 0 0;
    border-radius: 50%;
    background-color: $color-border;
    color: #fff;
    display: block;
    font-size: 12px;
    text-align: center;
        &:hover{        
        color: #fff;
        font-size: 12px;
        }
}
.cart-drawer__total-price {
    padding: 10px 0;
    text-transform: uppercase;
    font-weight: 700;
    overflow: hidden;
    font-size: 16px;
    letter-spacing: 1px;
    text-align: center;
}
.cart-drawer__total-text {
    display: inline-block;
    padding: 0 2px;
}
.cart-drawer__total-amount {
    display: inline-block;
    padding: 0 2px;
}
.cart-drawer__buttons {
    overflow: hidden;
    margin: 0 -3px;
    // .button {
    //     width: 50%;
    //     float: left;
    //     text-align: center;
    // }
}
.cart-drawer__footer {
    width: 100%;
    left: 0;
    bottom: 0;
    padding: 10px 0;
}
.cart-drawer__no-item{
    display: none;
    padding: 30px 0;
    text-align: center;
    letter-spacing: 1px;
    &.cart-drawer__no-item_visible{
        display: block;
    }
}

body{
    .drawer-overlap{
        position: fixed;
        width: 100%;
        height: 100vh;
        z-index: -1;
        background-color: rgba(0,0,0,0);
        transition: 500ms;
        top: 0;
        visibility: hidden;
    }
    &.drawer-open{
        .cart-drawer{
            transform: none;
        }

        .drawer-overlap{
            z-index: 100;
            background-color: rgba(0,0,0,.6);
            visibility: visible;
        }
    }
}

.cart-drawer__scroll{
    overflow-y: scroll;
    max-height: calc(100vh - 30px - 32px - 128px);
    margin-right: -30px;
    padding-right: 15px;
    
    @media(max-width: 767px){
        margin-right: -15px;
    }
}


// Dynamic checkout buttons
.cart-drawer__dynamic-payment-buttons {
    margin-bottom: 10px;
    width: 100%;
    float: left;
    margin-top: 15px;
    padding-right: 10px;
    @media(max-width: 767px){
        padding-right: 0;
    }
    .shopify-cleanslate{
        ._1mqqPYZtCQtz5_i0b-po4l{
            display: block !important;
            ._3TUeZPsTWjDxakSmeDcA4D{
                display: block;
                width: 100%;
                float: left;
                margin-bottom: 5px;
            }
        }
        @media(max-width: 767px){
           .iZJMuEDN4NxKS3mrxcBP9{
               ._2PfRg7DFvcstLFRNRf5W1e{
                   height: 40px !important;
                   overflow: hidden;
                   position: relative;
                   border-radius: 5px;
                   ._1M9S34W-UyhhDRRQQiV3RH{
                    height: 40px !important;
                   }
                   iframe{
                    position: absolute;
                    left: 0;
                    top: 50%;
                    transform: translateY(-50%);
                   }
               }
           }
        }
    }
}