.form__group {
    width: 100%;
    margin-bottom: 10px;
}
.form__input{
    height: 35px;
    width: 100%;
    padding: 0 10px;
    font-family: $font-body;
}
.form__label{
    width: 100%;
    display: block;
}
.form__url{
    color: $color-body-text;
    &:hover{
        color: $color-body-text;
        text-decoration: none;
    }
}
.errors{
    ul{
        list-style: none;
        padding-left: 0;
        li{
            color: #ff0000;
        }
    }
}
.form__button-wrapper {
    margin-top: 15px;
}


@media(max-width: 767px){
    .form__register-text{
        margin-top: 25px;
    }
}


.form{
    input[type=text], input[type=email], input[type=tel], select, textarea{
        width: 100%;
        height: 35px;
        padding: 0 10px;
        border: 1px solid $color-border;
        // margin-bottom: 15px;
    }
    label{
        margin-bottom: 3px;
    }
    textarea{
        height: 105px;
        padding: 5px 10px;
    }
}